import Vue from 'vue'
import VueRouter from 'vue-router'
import JuegosView from '@/views/JuegosView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: JuegosView
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
